import { createWebHistory , createRouter  } from 'vue-router'
// import indexGuide from '@/views/indexGuide.vue'

// 라우터 설계
const routes = [
    { 
      path : '/',
      name: '/',
      component: () => import('@/views/indexGuide.vue'),
    },{ 
      path : '/indexGuide',
      name: '/indexGuide',
      // component: indexGuide,
      component: () => import('@/views/indexGuide.vue'),
    },
    { 
      path : '/DArending',
      name: '/DArending',
      component: () => import('@/views/DA/DArending.vue'),
    }
]
export const router = createRouter({
  history: createWebHistory(),
  base: process.env.BASE_URL,
  routes,
});

